<template>
  <div class=" space-between flex center">
    <div class="flex align-center"> 
    <el-menu mode="horizontal" style="border:none" class="el-menu-demo font no-border">
      <!-- 首页 -->
      <el-menu-item>
        <router-link to="/index">index</router-link>
      </el-menu-item>
      <!-- 歌单 -->
      <el-menu-item>
        <router-link to="/collections">collections</router-link>
      </el-menu-item>
      <!-- 搜索 -->
      
    </el-menu>
    <el-input style="width:35%;margin-right:5px" v-model="keyword"></el-input>
    <span @click="search" class="font search">
          &nbsp;&nbsp; <i class="el-icon-search"></i> search </span>
        </div>
       
    <!-- 消息 -->
    <el-menu class="el-menu-demo no-border" style="border:none" mode="horizontal">
      <!-- <el-menu-item @click="test">
        <i class="el-icon-message"></i>message
      </el-menu-item>
      <el-menu-item @click="test">
        <i class="el-icon-message"></i>friends
      </el-menu-item>-->
 

       
      <!-- 登录窗 -->
      <el-menu-item @click="dialogVisible = true" v-if="!flagLogin">login</el-menu-item>

      <el-menu-item @click="logout" v-else>logout</el-menu-item>

      <el-dialog title="提示" :visible.sync="dialogVisible" width="30%" :modal-append-to-body='false'>
        <el-form
          ref="form"
          :model="form"
          :rules="rules"
          label-width="30%"
          style="padding-right:10%"
        >
          <el-form-item label="name" prop="name">
            <el-input v-model="form.name"></el-input>
          </el-form-item>

          <el-form-item label="password" prop="name">
            <el-input type="password" v-model="form.password"></el-input>
          </el-form-item>

          <el-form-item class="flex_end">
            <el-button type="primary" @click="cancle">取消</el-button>
            <el-button type="primary" @click="login">登录</el-button>
          </el-form-item>
        </el-form>
      </el-dialog>
      <!-- 注册 -->
      <el-menu-item @click="$router.push({name:'register'})">register</el-menu-item>
    </el-menu>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: "",
        password: ""
      },
      rules: {
        name: [
          
          { min: 1, max: 12, message: "别空", trigger: "blur" }
        ]
      },

      flagLogin: undefined,
      dialogVisible: false,
      keyword: ""
    };
  },
  methods: {
    cancle(){
      this.dialogVisible = false;
      this.form.name=""
      this.form.password=""

    },
    logout() {
      if (["/collections", "/message"].indexOf(this.curPath) != -1) {
        this.$router.push({ name: "index" });
      }
      this.$store.commit("setUid", "");
      this.flagLogin = false;
    },
    search() {
        var re= /select|update|delete|exec|count|’|"|=|;|>|<|%/i;
        if (re.test(this.keyword.toLowerCase())||re.test(this.keyword.toLowerCase())    ){
          alert("请您不要输入特殊字符和SQL关键字！");
         this.keyword=''
          return ;
      }
      this.$router.push({
        name: "search",
        params: { keyword: this.keyword, type: "1" }
      });
    },

    async login() {
        var re= /select|update|delete|exec|count|’|"|=|;|>|<|%/i;
      if (re.test(this.form.name.toLowerCase())||re.test(this.form.password.toLowerCase())    ){
          alert("请您不要输入特殊字符和SQL关键字！");
          this.form.name=''
          this.form.password=''
          return ;
      }

      var content = await this.$http.post("api/login", this.form);

      this.form.password=""
      if (content.data.mes == "suc") {
        
        this.$store.commit("setUid", content.data.result[0].id);
        alert("成功");
        this.dialogVisible = false;
        this.flagLogin = true;
        this.form.name=""
      
      }
    }
  },

  
  created() {
    if (this.$store.state.uid) this.flagLogin = true;
    else this.flagLogin = false;
  },
  computed: {
    curPath: function() {
      return this.$route.path;
    }
  },
  name: "myheader"
};
</script>


