<template>
  <div style="padding:20px;background-color:white">
    <div v-if="album"  class="flex" style="width:100%;margin-bottom:20px">
      <img style="display:inline-block" :src="album.picUrl+'?param=180y180'" alt />
      <div class="text">
        <h2 class="cname">{{album.name}}</h2>
        <br/>
        <h3 class="alias" style="white-space:nowrap; ">{{album.alias[0]}}</h3>
        <p>歌手:<span class="link" @click="$router.push({
        name: 'artist',
        params: {id:album.artist.id}
      })">{{album.artist.name}}</span></p>
        <p>发行时间:{{new Date(album.publishTime).format("yyyy-MM-dd")}}</p>
        <p>发行公司:{{album.company}}</p>
        <div>
          <button class="button" @click="$bus.$emit('playAlbum', songs)">播放</button>
        </div>
      </div>
    </div>
    <list :songs="songs" :columns="columns" :showHead="true" :paging="false"/>
  </div>
</template>

<script>
import list from "./list.vue";
export default {
  data() {
    return {
      album: undefined,
      songs: [],
      columns: [
        { text: "name",label: "歌名",width:500},
       
        {
          text: "name",
          label: "artist",
          array: "artists",
          isArray: true,
          linkId: "id",
          aformat: true
        },
         { text: "duration", label: "时长",}
      ]
    };
  },
  components: {
    list
  },
  methods: {
    async getAlbum(params) {
      var content = await this.$http.get(
        "album?id=" + params.id
      );
      this.album = content.data.album;
     
      this.songs = content.data.songs.map(({ ar, name, id, dt,al }) => {
        return {
          name,
          id,
          artists: ar,
          album:al,
          duration: this.format(dt / 1000)
        };
      });
   
    },
    format(value) {
      if (!value) return "0:00";
      var v = Math.floor(value % 60);
      v = v < 10 ? "0" + v : v;
      var m = Math.floor(value / 60);
      m = m < 10 ? "0" + m : m;
      return "" + m + ":" + v;
    }
  },
  mounted() {
    this.getAlbum(this.$route.params);
  },
  name: "album"
};
</script>
<style scoped>
*{
    padding: 0px;
    color:rgb(102, 102, 102);
}
.test {
  white-space: nowrap;
}
.text{
    text-align:left;padding:20px;
    height:180px;
    font-size:14px;
    flex:1;
    font-size: 12px;
    line-height: 18px;
}
.link{
  cursor: pointer;
}
.cname {
  
  font-weight: normal;
  font-size: px;
  color: #333;
  font-family: Arial, Helvetica, sans-serif;
}
.button{
    margin-top:20px;
    height:30px;
    width:60px;border:1px gray solid;
    border-radius:10%;
    background-color:rgb(248,248,248)
}
.alias {

  max-width: 23%;
  
  font-size: 14px;
  line-height: 32px;
  color: #999;
  font-weight: normal;
}
</style>