<template>
  <el-container>
    <el-aside width="18%">
      <el-card class="box-card" style=" border-bottom:none" body-style="padding:0px" shadow="never">
        <!-- 歌单头 -->
        <div slot="header">
          <h4 class="inline">Collections</h4>
          <i
            class="el-icon-delete"
            style="float: right; padding: 3px 5px"
            @click="dialogVisible2 = true"
          ></i>
          <i
            class="el-icon-circle-plus"
            style="float: right; padding: 3px 5px"
            @click="dialogVisible = true"
          ></i>
        </div>

        <!-- 歌单 -->
        <div
          v-if="collections.length"
          v-for="(list,i) in collections"
          :key="i"
          :class="['text',i==curCollection?'activate':'']"
          @click="chooseCollection(i)"
        >{{list.name}}</div>
        <!-- <div v-else :class="['text']" >
          no collection
        </div>-->

        <!-- 新歌单dialog -->
        <el-dialog title="提示" :visible.sync="dialogVisible" width="30%">
          <el-input v-model="collectionName" placeholder="请输入歌单名"></el-input>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false">取 消</el-button>
            <el-button type="primary" @click="createCollection">确 定</el-button>
          </span>
        </el-dialog>
        <!-- 删除歌单 -->
        <el-dialog title="提示" :visible.sync="dialogVisible2" width="30%">
          <el-radio-group v-model="deleteId">
            <el-radio v-for="(c,i) in collections" v-bind:key="i" :label="c.id">{{c.name}}</el-radio>
          </el-radio-group>
          <span slot="footer" class="dialog-footer">
            <el-button @click="dialogVisible = false;deleteId=undefined;">取 消</el-button>
            <el-button type="primary" @click="deleteCollection">确 定</el-button>
          </span>
        </el-dialog>
      </el-card>
    </el-aside>

    <el-main>
      <list
        v-if="collections.length"
        :songs="collections[curCollection].songs"
        :columns="columns"
        :type="'collections'"
        :parentFunction="deleteInList"
      />
    </el-main>
  </el-container>
</template>

<script>
import list from "./list.vue";
import { mapState } from 'vuex'
export default {
  data() {
    return {

      collectionName: '',

      deleteId: undefined,
      columns: [
        { label: 'name', text: 'name' },
        { label: 'artist', text: 'artists[0].name', linkId: 'artists[0].id', aformat: true },
        { label: 'album', text: 'album.name', linkId: 'album.id', aformat: true }],
      dialogVisible: false,
      dialogVisible2: false,

    };
  },
  components: {
    list
  },
  beforeMount() {

    this.chooseCollection(0)

  },
  computed: mapState({ collections: state => state.collections, curCollection: state => state.curCollection }),
  methods: {


    chooseCollection(index) {
      this.$store.commit('setCurCollection', index)
    
    },
   
    async deleteInList(row, index) {
      var content = await this.$http.post(
        "api/deleteInList",
        { songId: row.id, id: this.collections[this.curCollection].id }
      );
      // 更新当前列表
      this.$store.commit('func', state => { state.collections[this.curCollection].songs.splice(index, 1) })
    },
    async createCollection() {

      this.dialogVisible = false;
      var content = await this.$http.post(
        "api/createCollection",
        { uid: this.$store.state.uid, collectionName: this.collectionName }
      );
      if (content.status == 200) {

        this.$store.dispatch('getCollections')

      }
    },
    deleteCollection() {
      
      this.dialogVisible2 = false;
      this.$http.delete(
        "api/deleteCollection", {          data: { id: this.deleteId }
      }
      ).then(() => {
        console.log(this.deleteId)
        this.deleteId = undefined;
        this.chooseCollection(0)
        this.$store.dispatch('getCollections');
        
      });


    }
  },

  name: "collections"
};
</script>
<style  scoped>
.text {
  transition: background-color 0.3s linear;
  cursor: pointer;
  font-size: 16px;
  padding: 5px 0;
  border-bottom: 1px solid #ebeef5;
}
.text:hover {
  background-color: #f5f7fa;
}
.el-main {
  padding: 0px;
  margin-left: 15px;
}
.clearfix:before,
.clearfix:after {
  display: table;
  content: "";
}
.clearfix:after {
  clear: both;
}
.inline {
  display: inline;
}
.activate {
  background: rgb(230, 230, 230) !important;
}
.box-card {
}
</style>