<template>
  <div v-if="artist" style="padding:30px;background-color:white; border-left: 1px solid #d3d3d3; 
  border-right: 1px solid #d3d3d3; " >
    <div class="name">
      <h2 class="cname">{{artist.name}}</h2>
      <h3 class="alias">{{artist.alias[0]}}</h3>
    </div>
    <div>
  
      <img
        v-if="artist"
        :src="artist.picUrl+'?param=620y300'"
        width
        style="    display: block;
    width: 620px;
    height: 300px"
      />
    </div>
    <el-tabs type="border-card">
  <el-tab-pane label="热门作品">
    <list :songs="hotSongs" :columns="columns" :type="'artist'"  />
  </el-tab-pane>
  <el-tab-pane label="所有专辑">
    <imgItem :items="albums" :clickHandle="$toAlbum" />
  </el-tab-pane>
  <el-tab-pane label="艺人介绍">
    <h2 class="brief">{{artist.name+"简介"}}</h2>
    <p class="article"> {{artist.briefDesc}}</p>
   
  </el-tab-pane>
 
</el-tabs>
    
  </div>
</template>

<script>
import list from "./list.vue";
import imgItem from "./imgItem"
export default {
  data() {
    return {
      hotSongs: [],
      albums:[],
      artist: undefined,
      columns: [{ text: "name" }, { text: "duration" }]
    };
  },
  components: {
    list,
    imgItem
  },
  mounted: function() {
    this.getArtist(this.$route.params);
    this.getAlbums(this.$route.params)
  },
  beforeRouteUpdate(to, from, next) {
    this.getArtist(to.params)
    return next();
  },

  methods: {
    async getArtist(params) {
      var content = await this.$http.get(
        "artists?id=" + params.id
      );
      this.hotSongs = content.data.hotSongs.map(({ ar, name, id, al, dt }) => {
        return {
          name,
          id,
          artists: ar,
          album: al,
          duration: this.format(dt / 1000)
        };
      });
    
      this.artist = content.data.artist;
      console.log(this.artist)
    },
    async getAlbums(params) {
      var content = await this.$http.get(
        "artist/album?id=" + params.id
      );
     
      this.albums = content.data.hotAlbums
      
      
    },
    format(value) {
      if (!value) return "0:00";
      var v = Math.floor(value % 60);
      v = v < 10 ? "0" + v : v;
      var m = Math.floor(value / 60);
      m = m < 10 ? "0" + m : m;
      return "" + m + ":" + v;
    }
  },

  name: "Artist"
};
</script>
<style  scoped>
.brief{
 font-size: 16px;
 margin: 28px 0 8px 0;
    color: #333;
  text-align: left;
}
.article{
      line-height: 25px;
    color: #666;
    text-indent: 2em;
}
.cname {
  float: left;
  height: 34px;
  line-height: 24px;
  font-weight: normal;
  font-size: px;
  color: #333;
  font-family: Arial, Helvetica, sans-serif;
}
.alias {
  float: left;
  max-width: 23%;
  padding-left: 10px;
  font-size: 14px;
  line-height: 32px;
  color: #999;
  font-weight: normal;
}
.name::after {
  content: "";
  display: block;
  height: 0;
  clear: both;
  visibility: hidden;
}
</style>