
<template>
  <div @click="test($event)">
    <el-table
      v-if="songs"
      :show-header="showHead? true:false"
      style="overflow:hidden"
      :data="paging?songs.slice(num-10,num):songs "
      :header-row-style="rowStyle"
      :row-class-name="rowClass"
      :cell-style="tableCellStyle"
      stripe
    >
      <!-- 图标区 -->
      <el-table-column width="150px" style="border:none">
        <template slot-scope="scope">
          <!-- 播放 -->
          <i class="el-icon-video-play" @click="$bus.$emit('play', scope.row,type)"></i>
          <span class="inactive i-group">
            <!-- 删除 -->
            <i
              v-if="type=='collections'"
              class="el-icon-delete"
              title="delete"
              @click="parentFunction?parentFunction(scope.row,scope.$index):()=>{}"
            ></i>
            <!-- 加入列表 -->
            <i
              class="el-icon-plus"
              @click="$bus.$emit('add_to_playlist', scope.row,type)"
              title="add to playlist"
            ></i>
            <!-- 收藏 -->
            <i class="el-icon-folder-add" title="collect" @click="show_dialog(scope.row)"></i>
          </span>
        </template>
      </el-table-column>
      <!-- 主数据 -->
      <el-table-column :label="item.label" v-for="(item,index) in columns" :key="index"
       :width="item.width?item.width+'px':''">
        <template slot-scope="scope">
          <!-- <span v-html="evil(scope.row,item.prop)|ellipsis"></span> -->
          <span
            :class="{'link':item.aformat}"
            v-html="item.aformat? linkFormat(item,scope.row):evil(scope.row,item.text)"
          ></span>
        </template>
      </el-table-column>
    </el-table>
    <!-- 分页 -->
    <div v-if="paging" style="margin-top:20px">
      <el-button
        circle
        icon="el-icon-arrow-left"
        v-if="!type=='result'"
        @click="curPage==1 ? curPage: curPage--"
      ></el-button>
      <span
        v-bind:class="['pages',curPage==p?'selected':'']"
        v-for="(p,i) in page"
        v-bind:key="i"
        @click="curPage=p"
      >{{p}}</span>

      <el-button
        circle
        icon="el-icon-arrow-right"
        v-if="!type=='result'"
        @click="curPage==page ?curPage: curPage++"
      ></el-button>
    </div>
    <!-- 收藏dialog -->
    <el-dialog title="收藏到歌单" :visible.sync="dialogVisible" width="30%">
      <el-radio-group v-model="radio">
        <el-radio v-for="(c,i) in $store.state.collections" v-bind:key="i" :label="c.id">{{c.name}}</el-radio>
      </el-radio-group>
      <span slot="footer" class="dialog-footer">
        <el-button @click="dialogVisible = false">取 消</el-button>
        <el-button type="primary" @click="collect">确 定</el-button>
      </span>
    </el-dialog>
  </div>
</template>

<script>
export default {
  data() {
    return {
      dialogVisible: false,
      radio: "",
      curPage: 1
    };
  },
  props: {    
    type: {
      type: String
    },
    songs: {
      type: Array
    },
    parentFunction: {
      type: Function
    },
    columns: {
      type: Array
    },
    showHead: { type: Boolean, default: false },
    paging:{
      type:Boolean,default:true
    }

  },
  computed: {
    page() {
      return Math.ceil(this.songs.length / 10);
    },
    num() {
      return this.curPage * 10;
    }
  },
 
  methods: {
    test(e) {

      if (e.target.nodeName.toLowerCase() == 'a') {
        e.stopPropagation()
        this.$router.push({
          name: e.target.dataset.name,
          params: JSON.parse(e.target.dataset.params)
        });
      }

    },
    evil(obj, prop) {

      return eval("obj." + prop);
    },
    rowStyle({ row, rowIndex }) {
      if (rowIndex === 0) {
        return "color:black;line-height:20px;";
      } else {
        return "";
      }
    },
    tableCellStyle() {
      return 'border: none;'
    },
    rowClass({ row, rowIndex }) {
      return "arow";
    },

    show_dialog(data) {

      this.dialogVisible = true;
      // 收藏
      this.collect = async () => {
        var content = await this.$http.post(
          "api/collect",
          {
            song: data,
            id: this.radio
          }
        );
        if (content.status == 200) {
        }
        this.dialogVisible = false;
      };
    },
    collect() { },
    linkFormat(column, row) {
      if (column.isArray) {
        let str = ''
        let length = row[column.array].length
        row[column.array].forEach((item, index) => {
          str += `<a 
        data-name=${column.label}
        data-params={"id":${this.evil(item, column.linkId)}}
         >
      ${this.evil(item, column.text)}
      </a>`;
          if (index != length - 1) str += "/"
        });
        return str
      }
      else return `<a 
        data-name=${column.label}
        data-params={"id":${this.evil(row, column.linkId)}}
         >
      ${this.evil(row, column.text)}
      </a>`;

    }
  },

  name: "list"
};
</script>
<style  scoped>
.inactive {
  opacity: 0;
}
.el-table__body {
  border-bottom: 2px green solid;
}
.el-table::before {
  height: 0px;
}
.arow:hover .inactive {
  opacity: 1;
}
i {
  font-size: 20px;
}
.i-group {
  margin-left: 10px;
}
.link {
}
.link:hover {
  text-decoration: underline;
  cursor: pointer;
}
.i-group > i {
  margin: 0 5px;
}
.pages {
  margin: 0 5px;
  cursor: pointer;
}

.selected {
  text-decoration: underline rgb(64, 158, 255);
  color: rgb(64, 158, 255);
}
</style>