<template>
  <div>

    <div class="img">
      <img :src="imgList[curIndex]" alt />
    </div>
    <div style="margin: 5px auto">
      <el-button
        :type="curIndex==i?'primary':'info'"
        circle
        v-for="n,i in imgList"
        v-bind:key="i"
        
        @click="change(i,$event)"
      ></el-button>
    </div>
  </div>
</template>
<script>
export default {
  name: "index",
  data() {
    return {
      imgList: [
      
      ],
      curIndex: 0
    };
  },
  methods: {
    change(i, e) {
      this.curIndex=i
    }
  }
};
</script>

