<template>
  <div class="center">
  
    <div class="center-vertical flex">
      <!-- 控制器 -->
      <!-- prev -->
      <i
        id="prev"
        class="el-icon-d-arrow-left"
        v-on:click="curIndex<=0 ? curIndex=list.length-1 : curIndex--"
      ></i>

      <i class="el-icon-video-play" v-show="paused" v-on:click="play"></i>
      <i class="el-icon-video-pause" v-show="!paused" v-on:click="pause"></i>

      <i
        id="next"
        class="el-icon-d-arrow-right"
        v-on:click="curIndex>=list.length-1 ? curIndex=0 : curIndex++;"
      ></i>

      <!-- 条 -->
      <div class="roll">
        <!-- 歌名歌手 -->
       
          <div class="artistMes">
            <span v-if="list.length"  >{{ list[curIndex].name}}</span>
            <span v-if="list.length" >--{{ list[curIndex].artists[0].name}}</span>
          </div>
      

        <el-slider
          :show-tooltip="false"
          v-model="percentage"
          @change="myVid.currentTime=(percentage*myVid.duration/100)"
        ></el-slider>
      </div>
      <!-- 时长 -->
      <span class="time">/</span>
      <!-- 下拉菜单 -->
      <el-dropdown placement="top-end" trigger="click">
        <i class="el-icon-tickets drop-icon" id="list"></i>
        <el-dropdown-menu slot="dropdown">
          <el-dropdown-item v-if="list.length==0">no item</el-dropdown-item>
          <el-dropdown-item v-else v-for="(song,i) in list" v-bind:key="song.id">
            <div class="drop-list" @click="change(i)">
              <el-row style="width:100%">
                <el-col :span="18">
                  <span>{{ song.name}}</span>
                </el-col>
                <el-col :span="6">
                 <router-link :to="{name:'artist',params:{id:song.artists[0].id}}" > {{ song.artists[0].name|ellipsis}}</router-link> 
                  <!-- <span @click="show">{{ song.artists[0].name|ellipsis}} </span>  -->
                </el-col>
              </el-row>
              <!-- 删除小按钮 -->
              <div class="delete-icon" @click.stop="pop(i)">
                <i class="el-icon-delete" style=" font-size:8px;"></i>
              </div>
            </div>
          </el-dropdown-item>
        </el-dropdown-menu>
      </el-dropdown>

      <!-- 音频 -->
      <audio id="video1" :src="list.length>0 ?list[curIndex].url:''" autoplay ></audio>
    </div>
  </div>
</template>

<script>
export default {
  data() {
    return {
      list: [],
      curIndex: 0,
      time: 0,
      percentage: 0,
      myVid: "",
      paused: undefined,
      interval: "",
      loop:true
    };
  },

  mounted() {
    this.myVid = document.getElementById("video1");
    this.myVid.onended = () => {
      if (this.list.length == 1) {
        this.percentage = 0;
        this.myVid.play();
      }

      document.getElementById("next").click();
    };

    this.$bus.$on("play", async (song, type) => {
      if (!this.list.length) {
        await this.add_to_playlist(song, type);
        this.clock();
        return;
      }
      if (this.list.length == 1) {
        this.list[0].id == song.id
          ? ""
          : await this.add_to_playlist(song, type);
        this.curIndex = this.list.length - 1;
        return;
      }
      // 如果已存在先删除
      let stringArray = this.list.map(item => {
        return item.id + "";
      });

      let index = stringArray.indexOf(song.id + "");

      if (index != -1) {
        let temItem = this.list[index];
        this.list.splice(index, 1);
        this.list.push(temItem);
      } else await this.add_to_playlist(song, type);
      this.curIndex = this.list.length - 1;
    });

    this.$bus.$on("add_to_playlist", this.add_to_playlist);
    this.$bus.$on("playAlbum", this.playAlbum);
  },
  beforeDestroy() {
    clearInterval(this.interval);
  },
  watch: {
    curIndex: function() {
      this.paused = false;
      this.percentage = 0;
      this.clock();
    }
  },

  // 主要监控条条
  methods: {
    pause() {
      this.paused = true;
      this.myVid.pause();
      clearInterval(this.interval);
    },
    show(){
    
    },
     playAlbum(songs) {
      this.curIndex = 0;
      // this.list= await Promise.all(songs.map(   (item)=>{
      //   let url=   this.getUrl(item.id)
      //    return{...item, url }

      // //   }))
  
        songs.forEach(item => {
          
         this.add_to_playlist(item);
         
        })
       
   
    },
    play() {
      this.paused = false;
      this.myVid.play();
      this.clock();
    },
    async getUrl(id) {
      var content = await this.$http.get(
        "song/url?id=" + id
      );

      return content.data.data[0].url;
    },
    change(i) {
      this.curIndex = i;
    },
    async add_to_playlist(song, type) {
      // 原来没有url，附加一项url
    
      let url = await this.getUrl(song.id);
      if (url) {
        song.url = url;
        await this.list.push(song);
        if (this.list.length == 1) this.clock();
      } else {

       if(this.loop) {
         alert("vip歌曲放不了呀");
       this.loop=false;
       setTimeout(()=>{this.loop=true},300) 
       }
       
      }
    },
    clock() {
      if (this.interval) clearInterval(this.interval);
      if (this.list.length) {
        this.interval = setInterval(() => {
          this.percentage += (0.5 / this.myVid.duration) * 100;
        }, 500);
      }
    },
    pop(index) {
      let prev = document.getElementById("prev");
      let list = document.getElementById("list");
      this.list.splice(index, 1);

      if (index <= this.curIndex) {
        if (index == this.curIndex) this.percentage = 0;
        prev.click();
        if (this.curIndex < 0) this.curIndex = 0;
      }
      // 重新打开防止错位
      list.click();

      setTimeout(() => {
        list.click();
      }, 200);
    }
  },
  name: "player"
};
</script>
<style  scoped>
.color {
  color: rgb(87, 87, 88);
}
.center-vertical {
  align-items: center;
  background-color: white;
  z-index: 12;
}

.center-vertical > i {
  font-size: 38px;
}
.time {
  margin-left: 20px;
  display: inline-block;
  width: 72px;
  margin-right: 40px;
}
.artistMes {
  height: 23px;
  text-align: left;
position: relative;
top: 8px;
}
.roll {
  margin-left: 20px;
  width: 100%;
  display: inline-block;
  vertical-align: middle;
}

.delete-icon {
  width: 22px;
  vertical-align: middle;
  cursor: pointer;
}
.drop-list {
  width: 500px;
  display: flex;
  align-items: center;
  justify-content: space-between;
  cursor: default !important;
}
</style>