import Vue from 'vue'
import VueRouter from 'vue-router'


import register from '../components/register.vue'
import index from '../components/index.vue'
import test from '../components/test.vue'
import collections from '../components/collections.vue'
import search from '../components/search.vue'
import Artist from '../components/Artist.vue'
import Album from '../components/Album.vue'

Vue.use(VueRouter)

const routes = [

  
  {
    path: '/register',
    name: 'register',
    component: register,
  },
  {
    path: '/artist/id=:id',
    name: 'artist',
    component: Artist,
  },
  {
    path: '/album/id=:id',
    name: 'album',
    component: Album,
  },
  {
    path: '/index',
    name: 'index',
    component: index,
  },
  {
    path: '/test',
    name: 'test',
    component: test,
  },
  {
    path: '/collections',
    name: 'collections',
    component: collections,
  },
  {
    path: '/search/keyword=:keyword&type=:type',
    name: 'search',
    component: search,
  },



]

const router = new VueRouter({
  routes
})
router.beforeEach((to, from, next) => {
  // if (to.path != '/collections' & to.path != '/message') {  return next(); }
  if ( to.path != '/message'&&to.path != '/collections') {  return next(); }
  
  const nameStr =  router.app.$options.store.state.uid
  
  if (!nameStr) {alert('请登录'); return  router.push({name: 'index'})}
  else return next();
})
export default router
