<template>
  <div class="content">
    <el-card
      shadow="never"
      :class="[size==130?'card':'card2']"
      :body-style="{ padding: '0px' }"
      v-for="(item,i) in items"
      :key="item.id"
      @click.native="clickHandle(item.id)"
    >
      <img class="albumImg" :src="item.picUrl+'?param='+imgSize" :alt="item.name" />
      <span style="font-size:12px" @click="show(item.id)">{{item.name|ellipsis(10)}}</span>
    </el-card>
  </div>
</template>

<script>
export default {

  props: {
    items: Array,
    size: {
      type: Number,
      default: 130
    },
    clickHandle: Function
  },
  mounted() {

  },
  methods: {
    show(e) {
      console.log(e)
    }
  },
  computed: {
    imgSize() {
      return this.size + 'y' + this.size
    }
  },
  name: 'imgItem',

}
</script>
<style scoped>
.content {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
}
.card img,
.card span {
  cursor: pointer;
}

.card2 img,
.card2 span {
  cursor: pointer;
}
.card2 > span {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.albumImg {
  border: 1px black solid;
  border-radius: 0px;
  display: block;
  margin: 0;
}
.card {
  width: 152px;
  margin: 10px 2%;
  border: none;
}
.card2 {
  width: 152px;
  border: none;
  margin: 10px 2%;
}
</style>