<template>
  <div style="background-color:white;min-height: 600px;">
   
    <el-tabs type="border-card" v-model="searchType" @tab-click="setType">
      <!-- 单曲 -->
      <el-tab-pane label="单曲" name="1">
        <list :type="'result'" :songs="songs" :columns="columns" />
      </el-tab-pane>
      <!-- 歌手 -->
      <el-tab-pane shadow="never" label="歌手" name="100">
         <imgItem :items="artists" :size="150" :clickHandle="toArtist" />
      </el-tab-pane>
      <!-- 专辑 -->
      <el-tab-pane label="专辑" name="10">
       <imgItem :items="albums" :clickHandle="toAlbum"/>
      </el-tab-pane>
      <!-- <el-tab-pane label="" name="fourth">用户</el-tab-pane> -->
    </el-tabs>
  </div>
</template>

<script>
import list from "./list.vue";
import imgItem from "./imgItem"
export default {
  data() {
    return {
      songs: [],
      searchType: this.$route.params.type,
      name: "n1",
      artists: [],
      albums: [],
      columns: [
        { label: "name", text: "name" },
        {
          label: "artist",
          text: "name",
          array:"artists",
          isArray:true,
          linkId: "id",
          aformat: true
        },
        {
          label: "album",
          text: "album.name",
          linkId: "album.id",
          aformat: true
        }
      ]
      // propsName:['scope.row.album.name']
    };
  },
  components: {
    list,
    imgItem
  },

  mounted() {
    
    this.sendXHR(this.$route.params)
  },
watch:{
  $route(to,from){
    console.log(to.params);
  }
},
   beforeRouteUpdate(to, from, next) {
    this.sendXHR(to.params)
    return next();
  },
  methods: {
    toArtist(id) {
      this.$router.push({ name: "artist", params: { id } });
    },
    toAlbum(id) {
      this.$router.push({ name: "album", params: { id } });
    },
    async sendXHR(params) {
      console.log(params)
      let { keyword, type } = params;
      var content = await this.$http.get(
        "search?keywords= " +
          keyword +
          "&limit=100" +
          "&type=" +
          type
      );
      let result=content.data.result
       switch (this.searchType) {
        case "1":
          this.songs=result.songs
          break;
        case "100":
           this.artists =result.artists
          break;
        case "10":
           this.albums =result.albums
          break;
      }
      if (content.status == 200) return content.data.result;
    },

  
    setType() {
      this.$router.push({
        name: "search",
        params: { ...this.$route.params, type: this.searchType }
      });

  
    }
  },

  name: "search"
};
</script>
<style  scoped>
.tesT {
 border-radius: 2%;
}
.card {
  width: 150px;
  margin: 10px 2%;
  border: none;
}
.card2 {
  width: 134px;
  border: none;
  margin: 10px 2%;
}
.content{
display:flex;flex-wrap: wrap;justify-content: space-between;
}
.card2 > span {
  overflow: hidden;
  white-space: nowrap;
  text-overflow: ellipsis;
}
.albumImg {
 border: 1px black solid;
 border-radius:0px;
 display:block;margin:0;
}
.card  img,.card  span{
  cursor: pointer;
}

.card2  img,.card2  span{
  cursor: pointer;
}
</style>