import Vue from 'vue'
import App from './App.vue'
import router from './router'
import './plugins/element.js'
import axios from 'axios'
import VueBus from 'vue-bus'
import Vuex from 'vuex'


axios.defaults.baseURL = 'http://mu.gz2vip.idcfengye.com/'
Date.prototype.format = function (fmt) { 
  var o = {
      "M+": this.getMonth() + 1, //月份 
      "d+": this.getDate(), //日 
      "h+": this.getHours(), //小时 
      "m+": this.getMinutes(), //分 
      "s+": this.getSeconds(), //秒 
      "q+": Math.floor((this.getMonth() + 3) / 3), // 季度
      "S": this.getMilliseconds() // 毫秒
  };

  // 根据y的长度来截取年
  if (/(y+)/.test(fmt)){ 
fmt = fmt.replace(RegExp.$1, (this.getFullYear() + "").substr(4 - RegExp.$1.length));
  }
  for (var k in o){
if (new RegExp("(" + k + ")").test(fmt)) fmt = fmt.replace(RegExp.$1, (RegExp.$1.length == 1) ? (o[k]) : (("00" + o[k]).substr(("" + o[k]).length)));
  }
  return fmt;
}
Vue.config.productionTip = false
Vue.prototype.$http = axios
Vue.prototype.$toAlbum = function(id){
  this.$router.push({ name: "album", params: { id } });
}

Vue.use(VueBus)
Vue.use(Vuex)
Vue.prototype.mywindow = window
Vue.filter('ellipsis', function (value,n=12) {
 
  
  if (!value) return "";
  if (value.length > n) {
    return value.slice(0, n) + "...";
  }
  return value;
})


const store = new Vuex.Store({
  state:sessionStorage.getItem('state') ? JSON.parse(sessionStorage.getItem('state')): {
    collections: [],
    uid: '',
    curCollection:'undefined'
  },
  mutations: {
    setUid(state, uid) {
      state.uid = uid

    },
    setCurCollection(state, curCollection) {
      state.curCollection = curCollection

    },
    setCollection(state, collection) {
      state.collections = collection
    },
    setSongs(state, {songs, index}) {

      state.collections[index].songs = songs
    }
    ,
    func(state, f) {
      console.log(typeof(f))
      f(state)
    }
  },
  actions: {
    async getCollections({ dispatch,commit, state }) {
      if (state.uid == '') { commit('setCollection', []); return }
      var content = await axios.get("http://127.0.0.1:3000/api/getCollections?uid=" + state.uid+"&time="+new Date().getTime()
      );
      if (content.status == 200) {
        console.log(content)
        let collections = content.data.map((item) => {
          return { ...item, songs: [] }
        })
        commit('setCollection', collections);
        dispatch('getSongsBycollection')
      }
    },
    async getSongsBycollection({ commit, state }){
      var content = await axios.get(
        "http://127.0.0.1:3000/api/getSongsBycollection?id=" + state.collections[state.curCollection].id
      );
      if (content.status == 200) {
        commit('setSongs', { songs: content.data.map((item) => { return JSON.parse(item.data) }), index: state.curCollection })
      }
    }
  },
 


})

new Vue({
  router,
  render: h => h(App),
  store,
}).$mount('#app')
