<template>
  <div class="box">
    <el-form ref="form" :model="form" label-width="80px">
    <!-- 名字 -->
    
        <el-form-item required>
          <el-input v-model="form.name">
            <template slot="prepend">user</template>
          </el-input>
        </el-form-item>
     
        <el-col class="line" :span="2">-</el-col>
      <!-- 密码 -->
        <el-form-item required>
          <el-input type="password" v-model="form.password">
            <template slot="prepend">password</template>
          </el-input>
        </el-form-item>
      <!-- 邮箱 -->
        <el-form-item required>
          <el-input v-model="form.email">
            <template slot="prepend">email</template>
          </el-input>
        </el-form-item>
     

      <el-form-item class="flex_end">
        <el-button type="primary" class="flex_end" @click="onSubmit">登录</el-button>
      </el-form-item>
    </el-form>
  </div>
</template>

<script>
export default {
  data() {
    return {
      form: {
        name: "",
        password: "",
        email:'',
      }
    };
  },
  methods: {
    async onSubmit() {
      var content = await this.$http.post("api/insert",this.form);
      if(content.status==200){
          console.log('注册成功')
      }
      
      
    }
  },

  name: "test"
};
</script>
<style  scoped>
.flex_end {
  display: flex;
  justify-content: flex-end;
}
.box {
  box-sizing: border-box;
  width: 100%;
  padding: 10% 25%;
}
</style>